.wecodeart-palette-options {
  display: flex;
  flex-wrap: wrap;
}
.wecodeart-palette-options__item {
  position: relative;
  height: 67px;
  margin-bottom: 15px;
  width: calc(50% - 5px);
}
.wecodeart-palette-options__item:hover .wecodeart-palette-options__item-delete {
  display: flex;
}
.wecodeart-palette-options__item:nth-child(odd) {
  margin-right: 10px;
}
.wecodeart-palette-options__item-delete {
  position: absolute;
  right: 0;
  top: 0;
  padding: 0 !important;
  background-color: red !important;
  color: #ffffff !important;
  display: none;
  text-decoration: none;
}
.wecodeart-palette-options__item-delete:hover {
  text-decoration: none;
  color: red;
  background-color: #ffffff;
}
.wecodeart-palette-options__item-modal .actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}
.wecodeart-palette-options__item-modal button {
  margin-left: 10px;
}
.wecodeart-palette-options__button {
  background-color: #fff;
  height: 100%;
  width: 100%;
  padding: 0;
  border: none;
  border-radius: 0;
  cursor: pointer;
}
.wecodeart-palette-options__button:focus {
  outline: none;
}
.wecodeart-palette-options__button.active {
  outline: 2px solid #08c;
}
.wecodeart-palette-options__button:nth-child(even) {
  margin-left: auto;
}
.wecodeart-palette-options__button-title {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.75);
  color: #fff;
  text-align: left;
  font-weight: 600;
  padding: 5px 10px;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wecodeart-palette-options__button-color {
  display: inline-block;
  width: 25%;
  height: 100%;
}

.wecodeart-palette-add {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}
.wecodeart-palette-add .customize-control-title {
  margin-top: 10px;
}
.wecodeart-palette-add .actions {
  display: flex;
  margin-top: 10px;
}
.wecodeart-palette-add .actions button {
  margin-right: 10px;
  display: flex;
  align-items: center;
}

.wecodeart-palette-colors {
  background: #fff;
  margin-top: 15px;
  margin: 0 -15px;
  padding: 20px 15px 12px;
}
.wecodeart-palette-colors .wca-color-component {
  margin-bottom: 10px;
}
.wecodeart-palette-colors .add-custom-color {
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  padding: 8px 0;
}
.wecodeart-palette-colors .add-custom-color .dashicon {
  margin-left: 10px;
}
.wecodeart-palette-colors hr {
  margin: 10px -15px;
  border-bottom: 0;
}
.wecodeart-palette-colors__reset {
  color: #555D66;
  text-decoration: none;
  padding: 7px 0;
}
.wecodeart-palette-colors__reset .dashicon {
  color: #DCDCDC;
}